
.journal__main-content {
    display: flex;
}

.journal__sidebar {
    background-color: $dark-grey;
    color: white;
    display: flex;
    flex-direction: column;
    height: 100vh;
    padding-left: 10px;
    padding-right: 10px;
    width: 450px;
}

.journal__sidebar-navbar {
    display: flex;
    justify-content: space-between;
}

.journal__sidebar-navbar h3 {
    font-weight: lighter;
}

.journal__new-entry {
    align-items: center;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 30px;
    width: 100%;

    transition: color .3s ease;

    &:hover {
        color: darken($color: white, $amount: 20);
    }

}

.journal__entries {
    margin-top: 30px;
    flex: 1 1 auto;
    overflow-y: scroll;
}
.journal__entries::-webkit-scrollbar {
    width: 6px;     
    height: 6px;  
}

.journal__entries::-webkit-scrollbar-thumb {
    background: lighten($color: $dark-grey, $amount: 30);
    border-radius: 5px;
}

.journal__entries::-webkit-scrollbar-thumb:hover {
    background: #b3b3b3;
    box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.2);
}

.journal__entry {
    background-color: white;
    border-radius: 4px;
    color: $dark-grey;
    display: flex;
    margin-bottom: 10px;
    overflow: hidden;

    transition: filter .4s ease;

    &:hover {
        filter: saturate( 160% )
    }
}

.journal__entry-picture {
    background-position: center;
    min-height: 75px;
    min-width: 70px;
}

.journal__entry-body {
    padding: 10px;
    width: 100%;

}

.journal__entry-title {
    font-size: 14px;
    font-weight: bold;
}

.journal__entry-content {
    font-size: 10px;
}

.journal__entry-date-box {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 10px;
}

.journal__entry-date-box span {
    font-size: 12px;
}

